        </div>
      </h4>
      <!--注册-->
      <div class="loginview-style">
        <input type="text"
               class="nameinput-style"
               :placeholder=temp
               v-for="temp in inputArray">
        <input type="submit"
               class="zhuce-style"
               value="注册"
               @click="touchregistration">
        <p class="sign-up-msg">点击 “注册” 即表示您同意并愿意遵守简书<br> <a target="_blank"
                                                            href="http://www.jianshu.com/p/c44d171298ce">用户协议</a> 和 <a
          target="_blank" href="http://www.jianshu.com/p/2ov8x3">隐私政策</a> 。</p>
      </div>
      <h6 class="shejiao-style">社交帐号直接注册</h6>

      <ul class="dishanfan-style">
        <li style="background-color: #d65049">
          <a href="">微博
          </a>
        </li>
        <li style="background-color: #37be20">
          <a href="">微信
          </a>
        </li>
        <li style="background-color: #5a87d5">
          <a href="">QQ
          </a>
        </li>
      </ul>

    </div>
    <!--登录-->
    <div class="login-style" v-if="!isLogin">

      <h4 class="title">
        <div class="normal-title">
          <a class="active"
             @click="touchdenglu"
             @mouseenter="changeColor('.active')"
             @mouseleave="changeColormouseleave('.active')">登录</a>
          <b>·</b>
          <a class="js-sign-up-btn"
             @click="touchzhuce"
             @mouseenter="changeColor('.js-sign-up-btn')"
             @mouseleave="changeColormouseleave('.js-sign-up-btn')">注册</a>
        </div>
      </h4>

      <div class="loginview-style" style="height: 190px;">
        <input type="text"
               class="nameinput-style"
               :placeholder=temp
               v-for="temp in loginArray">
        <input type="submit"
               class="zhuce-style"
               value="登录"
               style="background-color: #4d92d0"
        @click="touchlogin">
      </div>

      <div class="jizhuwo-style">
      <input type="checkbox"> <span>记住我</span> <a href="">登录遇到问题?</a>
      </div>

      <h6 class="shejiao-style">社交帐号登录</h6>

      <ul class="dishanfan-style" style="width: 260px;">
        <li style="background-color: #d65049">
          <a href="">微博
          </a>
        </li>
        <li style="background-color: #37be20">
