  </div>
   

</template>
npm install jquery
  <!-- vue -->
<script>
  import $ from '../../../../node_modules/jquery/dist/jquery'

  export default {
    data() {

      return {
        // 判断选择的是哪个btn
        selectbtn: "#active",
        // input name
        inputArray: ["你的昵称", "手机号", "设置密码"],
        loginArray:["手机号或邮箱","密码"],
        // 显示登录会注册
        isLogin: false

      }

    },

    createds: {},

    methods: {

      touchdenglu() {

        $(".js-sign-up-btn").css({
          "color": "#969696",
          "border-bottom-width": "0px"
        })

        $(".active").css({
          "color": "#e06e5d",
          "border-bottom-width": "2px"
        })

        this.selectbtn = ".active"

        this.isLogin = false

      },

      touchzhuce() {

        $(".active").css({
          "color": "#969696",
          "border-bottom-width": "0px"
        })

        $(".js-sign-up-btn").css({
          "color": "#e06e5d",
          "border-bottom-width": "2px"
        })

        this.selectbtn = ".js-sign-up-btn"

        this.isLogin = true

      },

      // 鼠标略过改变颜色
      changeColor(str) {

        $(str).css({
          "color": "#e06e5d",
          "border-bottom-width": "2px"
        })

      },

      //鼠标离开
      changeColormouseleave(str) {

        if (str != this.selectbtn) {

          $(str).css({
            "color": "#969696",
            "border-bottom-width": "0px"
          })

        }

      },

      touchlogin() {

        this.$router.push("/HomePage")

      },

      touchregistration() {
        alert("请切换到登录页面点击登录按钮!")

      }

    }

  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 根部视图 */
  .rootView {
    width: 100%;
    height: 100%;
    background-color: #f1f1f1;
  }
  h1 {
    color: #e08988;
    font-size: 50px;
    text-align: left;
    margin-left: 50px;
    padding-top: 30px;
  }

  .login-style {
    width: 400px;
    margin: 30px auto 0;
    padding: 50px 50px 30px;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0 0 8px rgba(0, 0, 0, .1);
    vertical-align: middle;
    display: inline-block;
  }

  .title {
    margin: 0 auto 50px;
    padding: 10px;
    font-weight: 400;
    color: #969696;
  }

  .active {
    color: #e06e5d;
    font-weight: 600;
    margin-right: 20px;
    font-size: 17px;
    padding-bottom: 5px;
    border-bottom: 2px solid #e06e5d;
    padding-left: 7px;
    padding-right: 7px;

  }

  .js-sign-up-btn {
    font-weight: 600;
    color: #969696;
    margin-left: 20px;
    font-size: 17px;
    padding-bottom: 5px;
    border-bottom: 2px solid #e06e5d;
    padding-left: 7px;
    padding-right: 7px;
    border-bottom-width: 0px;
  }

  .loginview-style {
    width: 300px;
    height: 273px;
    border-radius: 4px;
    overflow: hidden;
    border-top: 1px solid #c5c5c5;
  }

  .nameinput-style {
    width: 100%;
    height: 50px;
    padding-left: 20px;
    border: 1px solid #c5c5c5;
    border-top-width: 0;
    font-size: 13px;
    background-color: #f8f8f8;

  }

  .zhuce-style {
    width: 100%;
    height: 45px;
    background-color: #54c327;
    margin-top: 20px;
    border-radius: 50px;
    font-size: 17px;
    font-weight: 800;
    color: white;
  }

  .sign-up-msg {
    font-size: 12px;
    margin-top: 5px;
    line-height: 20px;
  }

  .sign-up-msg a {
    color: #5394d0;

  }

  .shejiao-style {
    color: #b5b5b5;
  }

  .shejiao-style:before {
    position: relative;
    content: "_________";
    margin-right: 10px;
    bottom: 5px;
    color: #b5b5b5;
  }

  .shejiao-style:after {
    position: relative;
    content: "_________";
    margin-left: 10px;
    bottom: 5px;
    color: #b5b5b5;
  }

  .dishanfan-style {
    width: 190px;
    height: 50px;
    margin: 15px auto 30px;

  }

  .dishanfan-style li {
    background-color: #2687eb;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    margin-left: 5px;
    margin-right: 5px;
    display: inline-block;
  }

  .dishanfan-style li a{
    line-height: 50px;
    font-size: 13px;
    color: white;
  }

  .jizhuwo-style {
    text-align: left;
    margin-bottom: 15px;
  }

  .jizhuwo-style span {
    font-size: 14px;
    color: #b4b1b4;
  }

  .jizhuwo-style a{
    font-size: 14px;
    color: #7b797b;
    float: right;
  }

</style>



